.wrapper {
    display: flex;
    padding: 25px 0;
    width: 100%;
    border-top: 1px solid #151F2A;
}

.icons {
    display: flex;
    font-size: 0;

    &.right {
        margin-left: auto;
    }

    svg {
        display: inline-block;
        margin-right: 20px;
        height: 28px;
    }

    svg:last-child {
        margin-right: 0;
    }
}
