.wrapper {
    position: relative;
    width: 100%;
    height: 80px;
    z-index: 1;
    border-bottom: 1px solid transparent;
    box-sizing: border-box;
    padding: 17.5px 0;

    &.isLogin {
        background: #121721;
        border-color: #1A212F;
    }
}

.content {
    display: flex;
    max-width: 1355px;
    margin: 0 auto;
    width: 100%;
    padding: 0 55px;
    box-sizing: border-box;
}

.logo {
    display: inline-flex;
    margin-top: auto;
    margin-bottom: auto;
    width: 149px;
    height: 40px;

    &:hover {
        opacity: 0.85;
    }
}

.links {
    display: inline-flex;
    margin-left: 78px;
}

.link {
    display: inline-flex;
    margin-right: 25px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    text-decoration: none;
    margin-top: auto;
    margin-bottom: auto;

    &:hover {
        opacity: 0.85;
    }
}

.button {
    margin: auto 0;
    font-weight: 700;
    font-size: 13px;
    line-height: 18px;
    color: #FFFFFF;
    padding: 13px 29px;
    border-radius: 3px;
    cursor: pointer;
}

.grey {
    margin-left: auto;
    margin-right: 10px;
    background: #222B3E;

    &:hover {
        background: linear-gradient(180deg, #1A212F 0%, #1D2638 100%, #151B28 100%);
        color: rgb(255,255,255,0.85);
    }
}

.green {
    background: #1EA912;
    
    &:hover {
        background: #249C1A;
        color: rgba(242, 242, 242, 0.9);    
    }
}