.modal {
    display: flex;
    margin: auto;
    flex-direction: column;
}

.overlay {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background: linear-gradient(180deg, rgba(5, 7, 11, 0.9) 8.06%, #0E121B 100%);
    backdrop-filter: blur(2px);
}

.head {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
}

.logo {
    display: inline-flex;
    flex: 1 1 0;
    width: 0;
    justify-content: center;
}

.welcome {
    display: inline-flex;
    margin: auto 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: #FFFFFF;
    flex: 1 1 0;
    width: 0;
}

.close {
    display: inline-flex;
    margin: auto 0;
    flex: 1 1 0;
    width: 0;
    justify-content: right;
    
    svg {        
        cursor: pointer;

        &:hover {        
            path {
                fill: #6a7895d9
            }
        }
    }    
}

.wrapper {
    display: flex;
    width: 774px;
    height: 666px;
    border-radius: 3px;
    overflow: hidden;
    background: #121721;
}

.banner {
    display: flex;
    width: 370px;
}

.form {
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
}

.title {
    display: flex;
    background: #1A212F;
    box-shadow: 0px 4px 81px rgba(47, 41, 52, 0.25);
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    padding: 20px 0 20px 20px;
}

.box{    
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
}

.loginDirectly {
    display: flex;
    padding: 20px 0;
    background: #1A212F;
    box-shadow: 0px 4px 81px rgba(47, 41, 52, 0.25);
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #6A7895;
    justify-content: center;
    align-items: center;
}

.loginDirectly span {
    cursor: pointer;
    display: inline-flex;
    margin-left: 15px;

    &:hover {
        opacity: 0.85;
    }
}