.wrapper {
    display: flex;
    padding: 25px 0;
    width: 100%;
    border-top: 1px solid #151F2A;    

    .links {
        display: flex;
    }
}

.list {
    display: flex;
    flex-direction: column;
    padding-right: 85px;
    
    &:last-child {
        padding-right: 0;
    }
}

.title {
    display: flex;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    padding-bottom: 5px;
}

.link {
    margin-top: 10px;
    text-decoration: none;
    display: flex;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #6A7895;
    
    &:hover {
        opacity: 0.85;
    }
}

.notice {
    display: flex;
    margin: auto 0 auto auto;

    svg:nth-child(1) {
        margin-right: 45px;
    }
}
