.modal {
    background: #121721;
    box-shadow: 0px 4px 74px rgba(0, 0, 0, 0.35);
    border-radius: 3px;
    padding: 20px;
    margin: auto;
    width: 781px;
    height: 215px;
    box-sizing: border-box;
}

.overlay {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background: linear-gradient(180deg, rgba(5, 7, 11, 0.9) 8.06%, #0E121B 100%);
    backdrop-filter: blur(2px);

}

.title {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}

.close {
    float: right;
    cursor: pointer;
    
    &:hover {        
        path {
            fill: #6a7895d9
        }
    }
}

.list {
    position: relative;
    width: calc(100% + 15px);
    margin-top: 20px;
    height: 135px;
}

.button {
    width: 237px;
    height: 59px;
    padding: 20px;
    background: #1A212F;
    border-radius: 3px;
    box-sizing: border-box;
    margin: 15px 15px 0 0;
    display: inline-flex;
    cursor: pointer;

    &:nth-child(3n) {
        margin-right: 0;
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
        margin-top: 0;
    }
    &:hover {
        background: linear-gradient(180deg, #1A212F 0%, #1D2638 100%, #151B28 100%);

        .flag,
        .name {
            opacity: 0.85;
        }
    }
}

.name {
    display: inline-flex;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    margin-left: 10px;
}

.flag {
    display: inline-flex;
}

.tick {
    display: inline-flex;
    margin-left: auto;
}

.selected {
    background: #232E43 !important;
    cursor: default !important;

    .flag,
    .name {
        opacity: 1 !important;
    }
}