.wrapper {
    display: flex;
    padding: 30px 0;
    width: 100%;
    border-top: 1px solid #151F2A; 
}

.information {
    display: flex;
    flex-direction: column;

    svg {
        width: 109px;
        height: 29px;
        margin-bottom: 10px;
    }
}

.text {
    width: 521px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: #6A7895;
}

.right {
    display: flex;
    margin: auto 0 auto auto;
    align-items: center;
    
    a:hover {
        opacity: 0.85;
    }
}

.email {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #6A7895;
    margin-left: 5px;
}

.links {
    display: flex;
    margin-left: 40px;
    margin-right: 40px;

    a {
        display: flex;
        margin-left: 20px;

        &:hover path {
            fill: rgba(106, 120, 149, 0.8509803922);
        }
    }
}
