.reset {
    display: flex;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    margin: 35px auto;
    color: #6A7895;
    width: 100%;
    justify-content: center;

    &:hover {
        opacity: 0.85;
    }
}

.form {
    display: flex;
    flex: 1 0 auto;
    padding: 20px;
    flex-direction: column;
}

.title {
    display: flex;
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: #8394B6;
    padding-bottom: 5px;
}

.buttons {
    display: flex;
    margin-top: 15px;
}

.button {
    display: inline-flex;
    cursor: pointer;
    border: 1px solid #222B3E;
    border-radius: 3px;
    box-sizing: border-box;
    padding: 15px 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    justify-content: center;
    
    &:hover {
        background: rgba(34, 43, 62, 0.25);
    }

    &:first-child {
        width: 140px;
        margin-right: 10px;
    }

    &:nth-child(2) {
        width: 212px;
    }

    svg {
        margin-right: 5px;
    }
}

.green {
    background: #1EA912;

    &:hover {
        background: #249C1A;
        color: rgba(242, 242, 242, 0.9);

        svg {
            opacity: 0.9;
        }
    }
}

.phoneInput {
    display: flex;
    width: 100%;

    .input:first-child {
        margin-right: 10px;

        input {        
            max-width: 95px;
            flex: unset;
        }
    }
}

.input {
    display: flex;
    width: 100%;
    margin-top: 10px;
    border-radius: 3px;
    background: #19202E;
    padding: 0 15px;
    box-sizing: border-box;
    align-items: center;

    input {
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        color: #FFFFFF;
        outline: unset;
        border: 0;
        padding: 20px 0;
        background: transparent;
        flex: 1 0 auto;

        &::placeholder {
            opacity: 1;
            color: #37435A;
        }
    }

    &.checked {
        border-radius: 3px 3px 0px 0px;
    }

    &.fill .icon path {
        fill: white !important;
    }

    &.good .state path {
        fill: #1FBF75 !important;
    }

    &.bad .state path {
        fill: #EA5353 !important;
    }
}

.icon {
    display: inline-flex;
    width: 19px;
    height: 19px;
    margin-right: 10px;

    path {
        fill: #51607C;
    }
}

.state {
    display: inline-flex;
    width: 19px;
    height: 19px;
    margin-left: 10px;
}

.topSwitch {
    display: flex;
    background: #1A212F;
    align-content: space-between;
    width: 100%;
}

.tabSwitch {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #8394B6;
    border-bottom: 1px solid transparent;
    padding-top: 7px;
    padding-bottom: 14px;
    cursor: pointer;
    width: 50%;

    svg {
        margin-right: 5px;

        path {
            fill: #8394B6;
        }
    }

    &.active {
        color: white;
        border-color: #776BF8;

        path {
            fill: #776BF8;
        }
    }
}

.checkBox {
    display: flex;
    background: #222B3E;
    padding: 13px 15px;
    cursor: pointer;
    border-radius: 0px 0px 3px 3px;

    svg {
        margin-left: auto;
    }
}

.checkText {
    padding: 2px 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #8394B6;
}

.buttonCall {
    cursor: pointer;
    display: flex;
    background: #776BF8;
    border-radius: 3px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    padding: 8px 10px;    
    color: #FFFFFF;
    justify-self: center;
    margin-left: 10px;
    
    svg {
        margin-right: 5px;
    }
}